export function urlBlogList () {
  return 'https://public-api.wordpress.com/rest/v1/sites/pradumangoyal.wordpress.com/posts/'
}
export function urlBlog (id) {
  return `${urlBlogList()}${id}/`
}
export function urlbackendBase () {
  return `http://pradumangoyal.pythonanywhere.com/`
}
export function urlEnsureCSRF () {
  return `${urlbackendBase()}ensure_csrf/`
}
export function urlContactMe () {
  return `${urlbackendBase()}contact_me/`
}
export function urlContactMeId (id) {
  return `${urlbackendBase()}contact_me/${id}`
}
export function urlIsAdmin () {
  return `${urlbackendBase()}is_admin/`
}
export function urlLogin () {
  return `${urlbackendBase()}api-auth/login/`
}